.production {
    background-color: #84A178;
    padding-top: 60px;
    padding-bottom: 50px;
    @media screen and (min-width: 768px) {
        padding-bottom: 60px;
    }
}
.uppertitle {
    font-family: 'Montserrat';
    font-weight: 700;
    font-size: 18px;
    line-height: 1.22;
    text-align: center;
    text-transform: uppercase;
    color: #FCFCFC;
    margin-bottom: 12px;
    @media screen and (min-width: 768px) {
        font-weight: 800;
        font-size: 22px;
        letter-spacing: 0.04em;
    }
    @media screen and (min-width: 1200px) {
        font-size: 30px;
        line-height: 1.23;
    }
}
.production__title {
    font-family: 'Seymour One';
    font-style: normal;
    font-weight: 400;
    font-size: 30px;
    line-height: 1.26;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #FCFCFC;
    display: block;
    margin: 0 auto 32px auto;
    width: 220px;

    @media screen and (min-width:768px) {
        font-size: 34px;
        letter-spacing: 0.06em;
        width: 454px;
        margin-bottom: 72px;

    }

    @media screen and (min-width: 1200px) {
        font-size: 36px;
        line-height: 1.25;
        margin-bottom: 82px;
        width: 481px;
    }
}

.production__content {
    @media screen and (min-width: 768px) {
        display: flex;
    }
}

.production__thumb {
    margin: 0 auto 40px auto;
    position: relative;
    background-color: linear-gradient(0deg, rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7)), #D9D9D9;
    box-shadow: 0px 5.30813px 19.9055px rgba(153, 114, 23, 0.25);
    border-radius: 140px;
    width: 279px;
    height: 278px;

    @media screen and (min-width: 768px) {
        margin: 0 38px 0 32px;
        border-radius: 165px;
        width: 330px;
        height: 330px;
    }

    @media screen and (min-width: 1200px) {
        margin: 0 109px 0 16px;
        box-shadow: 0px 8px 30px rgba(153, 114, 23, 0.25);
        border-radius: 250px;
        width: 490px;
        height: 490px;
    }
}

.production__image {
    position: absolute;
    border-radius: 140px;
    width: 279px;
    height: 278px;

    @media screen and (min-width: 768px) {
        border-radius: 1228px;
        width: 330px;
        height: 330px;
    }

    @media screen and (min-width: 1200px) {
        border-radius: 1852.2px;
        width: 490px;
        height: 490px;
    }
}


.production__text {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 1.75;
    letter-spacing: 0.04em;
    color: #FCFCFC;
    width: 220px;
    margin: 0 auto 4px auto;

    @media screen and (max-width: 767px) {
        text-align: center;
    }
    @media screen and (min-width:768px) {
        width: 334px;
        margin-bottom: 8px;
    }
    @media screen and (min-width: 1200px) {
        width: 463px;
    }
}
.production__information {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 1.58;
    text-align: center;
    color: #FCFCFC;
    margin: 0 auto 10px auto;
    width: 220px;

    @media screen and (max-width: 767px) {
        text-align: center;
    }

    @media screen and (min-width: 768px) {
        line-height: 1.75;
        width: 334px;
        margin-bottom: 8px;
    }
    @media screen and (min-width: 1200px) {
        font-size: 14px;
        line-height: 1.92;
        width: 463px;
    }
}
.production__paragraph {
    display: none;
    @media screen and (min-width: 768px) {
        display: block;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 1.92;
        letter-spacing: 0.04em;
        color: #FCFCFC;
        width: 336px;
        margin-bottom: 32px;
    }
    @media screen and (min-width: 1200px) {
        font-size: 14px;
        width: 457px;
        margin-bottom: 54px;
    }
}
.production__button {
    background-color: #F8D24E;
    border-radius: 10px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.28;
    align-items: center;
    text-align: center;
    color: #000000;
    width: 164px;
    height: 142px;
    padding: 12px 34px;
    display: flex;
    margin: 0 auto;

    @media screen and (min-width: 768px) {
        &:hover {
            background-color: #0A8817;
        }
        &:focus {
            background-color: #0A8817;
            color: #FCFCFC;
        }
        &:disabled {
            background-color: #ABABAB;
        }
    }
}

.production__arrow {
    fill: #000000;
    margin-left: 7px;
    @media screen and (min-width: 768px) {
        &:focus {
            fill: #FCFCFC;
        }
    }
}